.experience{
    display: flex;
    gap: 5rem;
    align-items: center;
    justify-content: center;
    height: 30vh;
    margin-bottom: 5rem;
}

.achivement{
     display: flex;
     flex-direction: column;
     align-items: center;
}

.circle{
    border-radius: 100%;
    width: 4rem;
    height: 4rem;
    background: white;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    font-weight: bold;
    margin-bottom: 2rem;
    color: black;
}

.circle::before{
    border-radius: 100%;
    content: '';  
    background-image: linear-gradient(to bottom, #87e6fb 0%, blue 100%);
    top: -8px;
    left: -8px;
    bottom: -8px;
    right: -8px;
    position: absolute;
    z-index: -1;
    box-shadow: var(--boxshadow);
}

.achivement span:nth-of-type(2){
    color: var(--blue);
    font-weight: bold;
}